* {
    margin: 0;
    padding:0;
    font-weight: 500;
    font-style: normal;
    font-family: "DM Sans", sans-serif;
}
.nftInputAmount {
    color: #ffffff;
    font-size: 20px;
    letter-spacing: 0px;
    padding: 10px;
    line-height: 1.2;
    max-width: 30vw;
    min-width: 30vw;
}

#myVideo {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 100vw;
    height: 100vh;
    z-index: -100;
  }


.mintBtn {
  max-width: 300px;
  min-width: 30vw;
  margin-top: 10px;
  padding: 5px 45px 5px 45px;
  background-color: rgba(0, 0, 0, 0.81);
  border-radius: 10px;
}

.mintBtn:hover {
    background-color: rgba(0, 0, 0, 0.2)
}

.mainContainer {
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    background-image: url("../public/testTv.png");
    background-size: 100% 100%;
}

.mintContainer {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    background-color: #ddd;
    padding: 10px;
    border-radius: 5px;
    opacity: 85%;
}